<template>
    <div class="device-info">
        <div class="infoItem"
             v-for="itemInfo in updateInfo"
             :key="itemInfo.id">


            <!--  display the property of the item  -->
            <span class="deviceProp" style="color:gray">{{itemInfo.deviceProperty}}</span>:

            <!--  if itemInfo has property "isCopied" then add clickListener and styles  -->
            <b v-if="itemInfo.isCopied"
               class="propValue"
               @click="copy(itemInfo.value)"
               style="color:#5f9de4;">{{itemInfo.value}}</b>

            <b v-else class="propValue">{{itemInfo.value}}</b>

        </div>
    </div>
</template>

<script>
    export default {
        name: "DeviceInfo",

        props: {

            device: {
                type: Object,
                default: function () {
                    return {
                        typeOfEquipment: '',
                        workStatus: '  ',
                        title: ' ',
                        producer: 'Ohaus',
                        model: 'AX-123',
                        responsibleUnit: '   ',
                        operationalUnit: '-  2.0',
                        MOL: '  ',
                        territory: '.-()',
                        serialNumber: 'B715976163',
                        GUID: '508b2a71-662e-4983-ae0c-3cb0c1cd21c5',
                        bimsID: '49db8db1-585f-4b9e-bbf0-8a59698edc8b',
                        tam: '-001234',
                    }
                }
            }
        },


        data() {
            return {}
        },


        computed: {

            /*
            * change data when a new device arrives
            */
            updateInfo() {
                return [
                    {deviceProperty: ' ', value: this.device.typeOfEquipment},
                    {deviceProperty: '', value: this.device.workStatus},
                    {deviceProperty: '', value: this.device.producer},
                    {deviceProperty: '', value: this.device.model},
                    {deviceProperty: '  ()', value: this.device.responsibleUnit},
                    {deviceProperty: ' ', value: this.device.operationalUnit},
                    {deviceProperty: '', value: this.device.MOL},
                    {deviceProperty: '', value: this.device.territory},
                    {deviceProperty: ' ', value: this.device.serialNumber},
                    {deviceProperty: 'GUID', value: this.device.GUID, isCopied: true},
                    {deviceProperty: 'Bims ID', value: this.device.bimsID, isCopied: true},
                    {deviceProperty: 'Tam', value: this.device.tam, isCopied: true},
                ]
            },
        },


        methods: {

            /*
            * opy a device property value to clipboard if it has an 'isCopied'attribute
            */
            copy(code) {
                navigator.clipboard.writeText(code)
                    .then(() => {
                        alert('Code was copied ' + code)
                    })
                    .catch(err => {
                        console.log('[ERROR] COPY', err);
                    });
            }
        }
    }
</script>

<style scoped>

    /*Analog line-height*/
    .infoItem {
        /*form*/
        margin-top: 7px;
    }

    .infoItem:first-child {
        /*form*/
        margin-top: 0px;
    }

    /*------------------*/

    .device-info {
        /*form*/
        box-sizing: border-box;
        padding: 20px 18px 20px 18px;
        width: 550px;
        height: auto;
        background-color: #F7F7F7;
        border-radius: 5px;

        /*font*/
        font-size: 14px;
        font-style: normal;
        font-weight: 500;
        line-height: 17px;
    }

    @media (max-width: 700px) {

        /*Analog line-height*/
        .infoItem {
            /*form*/
            margin-top: 6px;
        }

        .infoItem:first-child {
            /*form*/
            margin-top: 0px;
        }

        /*------------------*/
        .device-info {
            /*form*/
            width: 100%;
            height: auto;
        }

    }
</style>